.flip-card {
  background-color: transparent;
  width: 50px;
  height: 50px;
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flip-card.flip .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  cursor: default;
}

.flip-card-front {
  background-color: #2980b9;
  color: black;
}

.flip-card-back {
  background-color: rgb(53, 182, 124);
  color: white;
  transform: rotateY(180deg);
  text-align: center;
  font-size: 30px;
  line-height: 50px;
  font-weight: bold;
  box-shadow: inset 0 0 0 2px #fff;
}

.board {
  width: 206px;
  height: 206px;
  flex-wrap: wrap;
  display: flex;
  justify-content: space-between;
  border: solid 1px #ccc;
  position: relative;
}

.hidden {
  opacity: 0;
  transition: all 0.3s;
}

.not-start:after {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.5);
  content: "";
  z-index: 1;
}

.btn-start {
  z-index: 2;
  font-size: 20px;
  line-height: 30px;
  width: 150px;
  text-align: center;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  background: red;
  color: #fff;
  border-radius: 30px;
}

.div-amount span {
  text-align: right;
  display: inline-block;
  width: 140px;
  padding-right: 10px;
}

.div-amount input {
  width: 50px;
}